}

.setting-search.active {
    visibility: visible;
    opacity: 1
}

.setting-search form {
    width: 95%
}

.setting-search input {
    width: 100%;
    height: 40px;
    display: block;
    outline: none
}

.close-setting-search {
    padding: 10px 5px;
    -webkit-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out
}

.close-setting-search:hover {
    color: #aa80e3
}

#setting {
    background: white
}


a:hover .fa-power-off, a:hover .fa-cog{
    color: #aa80e3 !important;
}



